@import "~@blueprintjs/core/lib/css/blueprint.css";
@import "~@blueprintjs/datetime/lib/css/blueprint-datetime.css";
@import "~@blueprintjs/core/lib/scss/variables";

.filters {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  .date-range-container {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;

    margin-top: 40px;
    padding-bottom: 25px;
    box-shadow: 0 6px 3px -3px $dark-gray1;
  }

  .hotels {
    width: 100%;
    overflow-y: scroll;
    padding: 0 20px;
    margin-top: 4px;

    h4.clickable {
      cursor: pointer;
      margin: 9px 0;

      .selected-icon {
        margin-left: 10px;
      }
    }

    span.hotel-name {
      margin: 0;
      > span.distance {
        margin-left: 10px;
        color: $gray3;
      }
    }
  }

  h3 {
    width: 100%;
    text-align: center;
    padding-bottom: 10px;
    margin-bottom: 0;
    box-shadow: 0 3px 2px -2px $dark-gray1;
  }

  p {
    margin-top: 30px;
  }
}
