@import "~@blueprintjs/core/lib/css/blueprint.css";
@import "~@blueprintjs/core/lib/scss/variables";
@import "./variables";

.popover {
  .#{$ns}-menu {
    max-height: 400px;
    overflow: auto;
  }
  .#{$ns}-input {
    width: 80vw;
    min-height: 40px;
  }
  .#{$ns}-input-ghost {
    font-size: 14px;
  }
}

.no-wrap {
  white-space: nowrap;
}

.container {
  display: flex;
  flex-direction: row;
  height: 100vh;
}

.sidebar {
  max-width: $sidebar-width;
  min-width: $sidebar-width;
  background-color: $dark-gray3;
  display: flex;
  flex: 1 1 0;
}
.content {
  flex-grow: 1;
  height: 100vh;
  padding: 20px;
}

h2 {
  width: 100%;
  text-align: center;
  margin: 0;
}

.container {
  background-color: $dark-gray5;
}
