@import "~@blueprintjs/core/lib/css/blueprint.css";
@import "~@blueprintjs/table/lib/css/table.css";
@import "~@blueprintjs/core/lib/scss/variables";
@import "./variables";

$checkout-width: 100px;
$table-width: calc(100vw - #{$sidebar-width} - 5vw);

.row-header {
  width: 100px;
}

.grid {
  display: flex;
  flex-direction: row;
  position: relative;

  > div {
    display: flex;
    flex-direction: column;
    flex-grow: 1;

    > .table-header {
      table-layout: fixed;
      width: calc(#{$table-width} - #{$checkout-width});
    }

    > .table-container {
      max-height: 84vh;
      overflow-y: scroll;
      display: flex;
      align-items: stretch;
      flex-direction: column;

      > .html-table {
        table-layout: fixed;
        width: $table-width;
        margin-right: 40px;

        th {
          width: $checkout-width;
        }
      }
    }
  }

  position: relative;
  .checkout-text {
    transform: rotate(90deg);
    position: absolute;
    top: 106px;
    right: -10px;
  }

  .dates-label {
    letter-spacing: 0.5px;
    font-variant: small-caps;
    margin: 0;

    &.checkin {
      margin-bottom: 5px;
    }
  }
}
