@import "~@blueprintjs/core/lib/css/blueprint.css";
@import "~@blueprintjs/core/lib/scss/variables";

$bad_cost_background: darken($vermilion1, 15%);
$bad_cost_text: $red5;
$good_cost_background: darken($forest1, 10%);
$good_cost_text: lighten($forest5, 20%); // rgb(225, 239, 229);
$neutral_background: darken($gray1, 10%);
$impossible: $dark-gray5;
$header_background: $dark-gray2;

table.#{$ns}-html-table tr {
  td,
  th {
    p,
    div.price {
      margin: 0;
      text-align: center;
      line-height: 23px;
    }
  }

  > td {
    font-weight: 500;
    font-size: 18px;
    &.active,
    :active {
      font-weight: bold;
    }

    vertical-align: middle !important;

    > div.price {
      user-select: none;
    }

    &.clickable {
      cursor: pointer;

      &.cost-1 {
        background-color: $bad_cost_background;
        color: $bad_cost_text;
        &.tint {
          background-color: darken($bad_cost_background, 4%);
        }
        &.active:not(:active) {
          background-color: darken($bad_cost_background, 10%);
        }
        &:hover {
          background-color: darken($bad_cost_background, 8%);
        }
        &:active {
          background-color: darken($bad_cost_background, 12%);
        }
      }

      &.cost-5 {
        background-color: $good_cost_background;
        color: $good_cost_text;
        &.tint {
          background-color: darken($good_cost_background, 4%);
        }
        &.active:not(:active) {
          background-color: darken($good_cost_background, 10%);
        }
        &:hover {
          background-color: darken($good_cost_background, 8%);
        }
        &:active {
          background-color: darken($good_cost_background, 12%);
        }
      }
    }

    &.cost-2 {
      color: $bad_cost_text;
    }

    &.cost-4 {
      color: $good_cost_text;
    }

    &.cost-2,
    &.cost-3,
    &.cost-4,
    &.no-price {
      background-color: $neutral_background;
      &.tint {
        background-color: darken($neutral_background, 4%);
      }
      &.active:not(:active) {
        background-color: darken($neutral_background, 10%);
      }
      &:hover {
        background-color: darken($neutral_background, 8%);
      }
      &:active {
        background-color: darken($neutral_background, 15%);
      }
    }

    &.impossible {
      background-color: $impossible;
      &.tint {
        background-color: darken($impossible, 4%);
      }
      &.active:not(:active) {
        background-color: darken($impossible, 10%);
      }
      &:hover {
        background-color: darken($impossible, 8%);
      }
      &:active {
        background-color: darken($impossible, 15%);
      }
    }
  }

  > th {
    &.checkout-date {
      // width: 100px !important;
    }

    background-color: $header_background;
    &.tint {
      background-color: darken($header_background, 4%);
    }
    &.active:not(:active) {
      background-color: darken($header_background, 10%);
    }
    &:hover {
      background-color: darken($header_background, 8%);
    }
    &:active {
      background-color: darken($header_background, 15%);
    }
  }
}
