@import "~@blueprintjs/core/lib/css/blueprint.css";
@import "~@blueprintjs/core/lib/scss/variables";

.available-rooms {
  width: 700px;
  text-align: center;

  .table-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 70vh;
    overflow-y: scroll;
    margin-top: 20px;
  }

  span.space {
    width: 10px;
    display: inline-block;
  }

  > p {
    margin: 20px 0 25px 0;
  }
}
